import { useSelector, useDispatch } from 'react-redux';
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { clearCart, confirmOrder, createCustomer, fetchProvinces, hideModal, showModal } from '../../actions/actions';
import { Button, Modal, TextField, Box, Paper, Typography, FormControl, InputLabel, Select, MenuItem } from '@mui/material';

const modalStyle = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    boxShadow: 24,
    p: 4,
};

const OrderInfo = ({ onCancel }) => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const cart = useSelector(state => state.cart);
    const user = useSelector(state => state.user);
    const isModalVisible = useSelector(state => state.modal.isModalVisible);
    const provinces = useSelector((state) => state.provinces.provinces);
    // Định nghĩa state cho thông tin khách hàng và đơn hàng
    const [customerInfo, setCustomerInfo] = useState({
        fullName: '',
        phone: '',
        email: '',
        address: '',
        city: '',
        district: '',
        country: 'Vietnam'
    });

    const [orderConfirmation, setOrderConfirmation] = useState({ orderId: null, orderDate: null });
    const [selectedCity, setSelectedCity] = useState(null); // Thêm state để lưu trữ thành phố đã chọn
    const [districts, setDistricts] = useState([]); // State lưu danh sách quận/huyện
    const [selectedDistrict, setSelectedDistrict] = useState('');

    useEffect(() => {
        dispatch(fetchProvinces());

        // Đọc thông tin khách hàng từ sessionStorage
        const storedCustomerInfo = sessionStorage.getItem('customerInfo');
        if (storedCustomerInfo) {
            const customerData = JSON.parse(storedCustomerInfo);
            setCustomerInfo({
                fullName: customerData.fullName || '',
                phone: customerData.phone || '',
                email: customerData.email || '',
                address: customerData.address || '',
                city: customerData.city || '',
                district: customerData.district || '',
                country: 'Vietnam'
            });
            if (customerData.city) {
                setSelectedCity(customerData.city);
                const selectedProvince = provinces.find(province => province.name === customerData.city);
                if (selectedProvince) {
                    setDistricts(selectedProvince.districts || []);
                    if (customerData.district) {
                        setSelectedDistrict(customerData.district);
                    }
                }
            }
        }
    }, [dispatch, provinces]);


    //  Đổi ngày tháng
    const readableOrderDate = orderConfirmation.orderDate ? new Date(orderConfirmation.orderDate).toLocaleDateString() : null;

    //Hàm xử lý nút trên bảng
    const handleCancel = () => { if (onCancel) onCancel(); };
    const handleConfirm = () => {
        // Kiểm tra nếu giỏ hàng rỗng
        if (cart.items.length === 0) {
            alert("Giỏ hàng rỗng!");
            return; // Thoát khỏi hàm nếu giỏ hàng không có sản phẩm
        }

        // Kiểm tra xem người dùng đã đăng nhập chưa trước khi xác nhận đơn hàng
        if (!user.isLoggedIn) {
            alert(`Bạn chưa đăng nhập!`);
            navigate('/login');
            return;
        }

        const orderDetails = {
            products: cart.items.map(item => ({
                product: item.id,
                quantity: item.quantity
            })),
            totalQuantity: cart.items.reduce((total, item) => total + item.quantity, 0),
            total: cart.total
        };
        dispatch(confirmOrder(orderDetails))
            .then(response => {
                setOrderConfirmation({ orderId: response.orderId, orderDate: response.orderDate });
                dispatch(showModal());
            })
            .catch((error) => {
                console.error('Error:', error);
                alert(error.message);
            });
    };
    //Hàm xử lý đóng Modal
    const handleCloseModal = () => {
        dispatch(hideModal());
    };
    // Hàm xử lý hủy trên Modal
    const handleCancelModal = () => {
        dispatch(hideModal()); // Tắt modal
        if (onCancel) onCancel(); // Gọi callback onCancel nếu có
    };

    //Hàm xử lý trên Modal
    const handleInputChange = (e) => {
        setCustomerInfo({ ...customerInfo, [e.target.name]: e.target.value });
    };


    const handleModalConfirm = () => {
        // Kiểm tra xem email và phone đã được điền đầy đủ hay không
        const isComplete = customerInfo.email !== '' && customerInfo.phone !== '';

        if (!isComplete) {
            alert('Xin hãy nhập đầy đủ Email và Số điện thoại!');
            return;
        }

        // Include the order information with the customer details
        const customerData = {
            ...customerInfo,
            city: selectedCity, // Sử dụng thành phố đã chọn từ state
            district: selectedDistrict,
            orders: [{
                orderId: orderConfirmation.orderId,
                orderDate: orderConfirmation.orderDate
            }]
        };
        // Lấy token từ sessionStorage
        const token = sessionStorage.getItem('token');
        const orderId = orderConfirmation.orderId;
        const userId = sessionStorage.getItem('userId'); // Lấy userId từ Redux store hoặc sessionStorage

        // Kiểm tra token
        if (!token) {
            alert("Không có token xác thực!");
            return;
        }

        dispatch(createCustomer(customerData, orderId, userId, token))
            .then(response => {
                alert(response.message);;
                dispatch(clearCart());
                localStorage.removeItem('cart');
                dispatch(hideModal());
            })
            .catch(error => {
                console.error('Error:', error);
                alert(error.response?.data?.message || "Có lỗi xảy ra!");
            });
    };

    // Font Style
    const customFontStyle = {
        fontFamily: "'Happy Monkey', sans-serif",
    };
    // Item Name Style
    const itemNameStyle = {
        fontFamily: "'Happy Monkey', sans-serif",
        fontSize: '1.2rem',
        color: '#333',
    };
    // Item Quantity Style
    const itemQuantityStyle = {
        fontFamily: "'Happy Monkey', sans-serif",
        fontSize: '1.1rem',
        color: '#555',
    };
    //Button Confirm Style
    const buttonConfirmStyle = {
        backgroundColor: '#6c8e5d',
        '&:hover': {
            backgroundColor: '#388E3C',
        },
        fontFamily: "'Happy Monkey', sans-serif",
        fontSize: 'large',
        color: '#fef7d0',
        fontweight: 'bold',
        cursor: 'pointer',
        margin: '10px 0',
    };
    // Button Cancel Style
    const buttonCancelStyle = {
        backgroundColor: '#fcba03',
        '&:hover': {
            backgroundColor: '#ff0011',
        },
        fontFamily: "'Happy Monkey', sans-serif",
        fontSize: 'large',
        color: '#01723e',
        fontweight: 'bold',
        cursor: 'pointer',
        margin: '20px 0',
    };

    // Hàm này sẽ được gọi khi tỉnh/thành phố được chọn thay đổi
    const handleCityChange = (event) => {
        const value = event.target.value; // Lấy giá trị đã chọn từ sự kiện onChange
        setSelectedCity(value); // Cập nhật giá trị của selectedCity

        // Tìm danh sách quận/huyện dựa trên tỉnh/thành phố được chọn
        const selectedProvince = provinces.find(province => province.name === value);

        if (selectedProvince && selectedProvince.districts) {
            setDistricts(selectedProvince.districts); // Cập nhật danh sách quận/huyện
            setSelectedDistrict(''); // Reset giá trị quận/huyện đã chọn
        } else {
            setDistricts([]); // Nếu không tìm thấy danh sách, đặt danh sách quận/huyện rỗng
            setSelectedDistrict(''); // Reset giá trị quận/huyện đã chọn
        }
    };


    return (
        <div>
            <Box display="flex" justifyContent="flex-end" m={2}>
                <Paper sx={{ padding: 2, backgroundColor: '#fef7d0', boxShadow: 3 }}>
                    <Typography sx={{ ...customFontStyle, fontWeight: 'bold', fontSize: '2rem' }}>Xác nhận đơn hàng</Typography>
                    {cart.items.map(item => (
                        <div key={item.id}>
                            <span style={itemNameStyle}>{item.name}</span>
                            <span style={itemQuantityStyle}>- {item.quantity}</span>
                        </div>
                    ))}
                    <div style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.4rem' }}>Total: {cart.total}</div>
                    <Button variant="outlined" color="primary" onClick={handleConfirm} >Xác nhận</Button>
                    <Button variant="outlined" color="warning" onClick={handleCancel}>Hủy</Button>
                    <div>
                        {isModalVisible && (
                            <Modal
                                open={isModalVisible}
                                onClose={handleCloseModal}
                            >
                                <Box sx={modalStyle}>
                                    <h2 style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.6rem' }}>Nhập thông tin giao hàng</h2>
                                    <div style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.2rem' }}>Order ID: {orderConfirmation.orderId}</div>
                                    <div style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.2rem' }}>Order Date: {readableOrderDate}</div>
                                    <br />
                                    <TextField fullWidth label="Họ tên" name="fullName" onChange={handleInputChange} value={customerInfo.fullName} />
                                    <TextField fullWidth label="Số điện thoại" name="phone" onChange={handleInputChange} value={customerInfo.phone} />
                                    <TextField fullWidth label="Email" name="email" onChange={handleInputChange} value={customerInfo.email} />
                                    <TextField fullWidth label="Địa chỉ" name="address" onChange={handleInputChange} value={customerInfo.address} />
                                    <FormControl fullWidth>
                                        <InputLabel>Thành Phố</InputLabel>
                                        <Select
                                            name="city"
                                            value={selectedCity}
                                            onChange={handleCityChange}
                                        >
                                            {provinces.map((province) => (
                                                <MenuItem key={province.code} value={province.name}>
                                                    {province.name}
                                                </MenuItem>
                                            ))}
                                        </Select>
                                    </FormControl>
                                    <FormControl fullWidth>
                                        <InputLabel>Quận</InputLabel>
                                        <Select
                                            name="district"
                                            value={selectedDistrict}
                                            onChange={(e) => setSelectedDistrict(e.target.value)}
                                        >
                                            {districts.map((district) => (
                                                <MenuItem key={district.code} value={district.name}>
                                                    {district.name}
                                                </MenuItem>
                                            ))}
                                        </Select>
                                    </FormControl>
                                    <FormControl fullWidth>
                                        <Typography>{customerInfo.country}</Typography>
                                        <InputLabel>Quốc Gia</InputLabel>
                                    </FormControl>
                                    <br></br>
                                    <br />
                                    <Button onClick={handleModalConfirm} style={buttonConfirmStyle}>Xác nhận mua hàng</Button>
                                    <Button onClick={handleCancelModal} style={buttonCancelStyle}>Hủy mua hàng</Button>
                                </Box>
                            </Modal>
                        )}
                    </div>
                </Paper>
            </Box>
        </div>
    );
};

export default OrderInfo;