import * as React from 'react';
import SummarizeIcon from '@mui/icons-material/Summarize';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import AddReactionIcon from '@mui/icons-material/AddReaction';
import IconButton from '@mui/material/IconButton';
import Tooltip from '@mui/material/Tooltip';
import { Menu, MenuItem } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import Badge from '@mui/material/Badge';
import { useEffect } from 'react';

export default function IconNavBar() {
    const [cartItemCount, setCartItemCount] = React.useState(0);
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [isLoggedIn, setIsLoggedIn] = React.useState(false); // Trạng thái đăng nhập
    const navigate = useNavigate();

    // Retrieve the cart data from local storage
    const updateCartCount = () => {
        const cart = JSON.parse(localStorage.getItem('cart') || '[]');
        setCartItemCount(cart.length);
    };

    useEffect(() => {
        updateCartCount();
        // Xử lý sự kiện cập nhật số lượng trên icon giỏ hàng
        window.addEventListener('cartUpdated', updateCartCount);

        // Kiểm tra trạng thái đăng nhập từ sessionStorage
        const loggedIn = sessionStorage.getItem('isLoggedIn') === 'true';
        setIsLoggedIn(loggedIn);

        // Xóa số trên icon giỏ hàng
        return () => {
            window.removeEventListener('cartUpdated', updateCartCount);
        };
    }, []);

    // Hàm xử lý đăng xuất
    const handleLogout = () => {
        sessionStorage.removeItem('isLoggedIn'); // Xóa trạng thái đăng nhập khỏi sessionStorage
        sessionStorage.removeItem('token');
        sessionStorage.removeItem('refreshToken');
        sessionStorage.removeItem('userId');
        sessionStorage.removeItem('userRole');
        sessionStorage.removeItem('customerInfo');
        setIsLoggedIn(false); // Cập nhật trạng thái đăng nhập
        navigate("/");
        handleClose();
    };

    const handleMenu = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleSignUp = () => {
        // Logic để chuyển đến trang Sign Up
        navigate("/signup");
        handleClose();
    };

    const handleLogin = () => {
        // Logic để chuyển đến trang Login
        navigate("/login");
        handleClose();
    };

    const handleCart = () => {
        navigate("/cart");
    };

    const handleAdmin = () => {
        navigate("/admin");
    };

    //Lấy UserId để hiện thị
    const username = sessionStorage.getItem('userId') || 'Người Dùng';


    return (
        <div>
            <Tooltip title="Công Cụ Quản Lý">
                <IconButton
                    size="large"
                    edge="start"
                    color="black"
                    aria-label="menu"
                    sx={{ mr: 2 }}
                    onClick={handleAdmin}
                >
                    <SummarizeIcon />
                </IconButton>
            </Tooltip>
            <Tooltip title="Tài Khoản">
                <IconButton
                    size="large"
                    edge="start"
                    color="black"
                    aria-label="menu"
                    sx={{ mr: 2 }}
                    onClick={handleMenu}
                >
                    {isLoggedIn ? <AddReactionIcon /> : <AccountCircleIcon />}
                </IconButton>
            </Tooltip>
            <Tooltip title="Giỏ Hàng">
                <IconButton
                    size="large"
                    edge="start"
                    color="black"
                    aria-label="menu"
                    sx={{ mr: 2 }}
                    onClick={handleCart}
                >
                    <Badge badgeContent={cartItemCount} color="secondary">
                        <ShoppingCartIcon />
                    </Badge>
                </IconButton>
            </Tooltip>
            <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                open={Boolean(anchorEl)}
                onClose={handleClose}
            >
                <div>
                    {isLoggedIn ? (
                        <>
                            <MenuItem disabled>{`Đã Đăng Nhập: ${username}`}</MenuItem>
                            <MenuItem onClick={handleLogout}>Đăng Xuất</MenuItem>
                        </>
                    ) : (
                        <>
                            <MenuItem onClick={handleSignUp}>Đăng Ký</MenuItem>
                            <MenuItem onClick={handleLogin}>Đăng Nhập</MenuItem>
                        </>
                    )}
                </div>
            </Menu>
        </div>
    );
}